* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mentor-photo {
  width: 150px;
}
.sign-btn {
  display: block;
}
.home {
  width: 500px;
  margin: 50px auto;
}

h3 {
  margin: 10px;
  text-align: center;
}

.home p {
  padding: 5px;
}
.navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 115px;
  background-color: darkred;
  padding: 0 100px;
}

.navigation-link {
  text-decoration: none;
  color: #000;
  font-weight: 900;
  margin: 0 20px;
  border: 5px solid #000;
  border-radius: 10%;
  padding: 10px;
  background: #eee;
}

.active {
  background: darkgray;
  color: #fff;
}

.buttons-container {
  margin: auto;
}
.authorized-user {
  height: inherit;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.authorized-user img {
  height: 60px;
  border-radius: 50%;
  margin: 5px 0;
  box-sizing: content-box;
  border: 3px solid #000;
}

.auth-btn {
  width: 70px;
  height: 30px;
  cursor: pointer;
  border: 3px solid #000;
  padding: 5px;
  line-height: 15px;
}
.mentor-link {
  display: block;
  text-decoration: none;
  padding: 2px 10px;
  color: #000;
  font-weight: 600;
  width: 0;
  white-space: nowrap;
}

.search-input {
  margin: 10px auto;
  display: block;
  width: 300px;
  height: 50px;
  background: rgba(238, 238, 238, 0.932);
  border: none;
  font-size: 10pt;
  color: rgb(41, 41, 41);
  border-radius: 5px;
  font-weight: 900;
  font-size: 15px;
  text-align: center;
}

.search-input::-webkit-input-placeholder {
  font-weight: 900;
}

.search-input::-ms-input-placeholder {
  font-weight: 900;
}

.search-input::placeholder {
  font-weight: 900;
}
table, td {
  border: 3px solid rgb(172, 172, 172);
  border-collapse: collapse;
  font-weight: 600;
}

table {
  margin: 0 auto;
}

tr {
  text-align: center;
}

td {
  padding: 5px;
}

.checked {
  background-color: greenyellow;
}

.unchecked {
  background-color: red;
}

.checking {
  background-color: rgba(218, 79, 25, 0.719);
}

.todo {
  background-color: rgb(151, 151, 151);
}

.in-progress {
  background-color: rgb(217, 255, 0);
}


h2 {
  text-align: center;
  margin: 10px;
}
